import utils from '../common/utils/utils';
import { defaultProps, defaultOptions } from '../common/component-handler/press-component';

export default {
  name: 'PressSkeleton',
  options: {
    defaultOptions,
  },
  props: {
    row: {
      type: Number,
      default: 0,
    },
    title: Boolean,
    avatar: Boolean,
    loading: {
      type: Boolean,
      default: true,
    },
    animate: {
      type: Boolean,
      default: true,
    },
    avatarSize: {
      type: String,
      default: '32px',
    },
    avatarShape: {
      type: String,
      default: 'round',
    },
    titleWidth: {
      type: String,
      default: '40%',
    },
    rowWidth: {
      type: null,
      default: '100%',
    },
    avatarClass: {
      type: String,
      default: '',
    },
    titleClass: {
      type: String,
      default: '',
    },
    rowClass: {
      type: String,
      default: '',
    },
    ...defaultProps,
  },
  emits: [],
  data() {
    return {
      utils,
      isArray: false,
      rowArray: [],
    };
  },
  computed: {

  },
  watch: {
    row: {
      handler(val) {
        this.rowArray = Array.from({ length: val });
      },
      immediate: true,
    },
    rowWidth: {
      handler(val) {
        this.isArray = val instanceof Array;
      },
      immediate: true,
    },
  },
};
